.NavItem {
	margin: 10px 0;
	box-sizing: border-box;
	display: block;
	width: 100%;
}

.NavItem a {
	color: #8f5c2c;
	text-decoration: none;
	width: 100%;
	box-sizing: border-box;
	display: block;
}

.NavItem a:hover, .NavItem a:active, .NavItem a.active {
	color: #40a4cb;
}

/*For larger devices*/
@media (min-width: 500px) {
	.NavItem {
		margin: 0;
		/*Needed to center the link vertically*/
		display: flex;
		height: 100%;
		width: auto;
		align-items: center;
	}

	.NavItem a {
		color: white;
		height: 100%;
		width: auto;
		padding: 16px 10px;
		/*Create bottom border for the hover effect*/
		border-bottom: 4px solid transparent;
	}

	.NavItem a:hover, .NavItem a:active, .NavItem a.active {
		background-color: #8f5c2c;
		border-bottom: 4px solid #40A4C8;
		color: white;
	}
}