.Burger {
	width: 100%;
	height: 250px;
	margin: auto;
	overflow: auto;
	text-align: center;
	font-weight: bold;
	font-size: 1.2rem;
}

@media (min-width: 500px) and (min-height: 400px) {
	.Burger {
		width: 450px;
		height: 350px;
	}
}

@media (min-width: 1000px) and (min-height: 700px) {
	.Burger {
		width: 700px;
		height: 500px;
	}
}